import React from 'react'
import { Icon } from 'antd'
import './studentCard.css'
import { inject, observer } from 'mobx-react'
import moment from 'moment'
@inject('userStore')
@observer
export default class StudentCard extends React.Component {
  constructor() {
    super()
    this.state = {
      // disable: false,
      // friend: false
    }
  }

  render() {
    let title = ''
    let grade = ''
    let sex = ''
    const photo = this.props.stuInfo.avatar ? this.props.stuInfo.avatar : 'http://ufind-10067447.cossh.myqcloud.com/userAvatar/defaultAvatar.svg'
    let birth = ''
    if (this.props.stuInfo.title === 1) {
      title = '本科'
      grade = this.props.stuInfo.year
    } else {
      title = '硕士生'
      grade = this.props.stuInfo.year - 4
    }
    if (this.props.stuInfo.gender === 1) {
      sex = (
        <Icon styleName="man" type="man" />
      )
    } else {
      sex = (
        <Icon styleName="woman" type="woman" />
      )
    }
    if (this.props.stuInfo.birthday) {
      birth = (
        <span styleName="details">{moment(this.props.stuInfo.birthday['$date']).format('YYYY-MM-DD')}</span>
      )
    }
    return (
      <div styleName="student-card">
        <div styleName="info-list">
          <img styleName="photo" src={photo} />
          <span styleName="name">{this.props.stuInfo.realName}</span>
          {sex}
          <span styleName="details">{this.props.stuInfo.school}</span>
          <span styleName="details">{this.props.stuInfo.department}</span>
          <span styleName="details">{this.props.stuInfo.major}</span>
          <span styleName="details">{title}{grade}年级</span>
          {birth}
          <div styleName="contact">
            <Icon styleName="contact-icon" type="wechat" /><span>{this.props.stuInfo.Wechat}</span>
            <Icon type="qq" styleName="contact-icon" /><span>{this.props.stuInfo.QQ}</span>
          </div>
        </div>
      </div>
    )
  }
}



